import React from 'react';

type IIpadActiveContext = [
  activeIndex: number,
  setActiveIndex: (index: number) => void,
];

const context = React.createContext<IIpadActiveContext>([-1, () => {}]);

export const IpadActiveBarProvider = context.Provider;

export const useIpadActiveBarIndex = () => {
  const [activeIndex, setActiveIndex] = React.useContext(context);
  return [activeIndex, setActiveIndex] as IIpadActiveContext;
};
